import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Moveable from 'react-moveable';
import { useDrop } from 'react-dnd';
import std from '../../utils/standardValuesTransformOrigin.js';
import items from '../shared/itemsData';
import {
  addElements,
  deleteElement,
  updateElements,
} from '../../slices/sliceDisplays.js';

const pt = 0.75; // для перевода шрифта в печатный
const AreaDnd = () => {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const [stateDrop, setStateDrop] = useState();
  const [snappable, setSnappable] = useState();
  const [elements, setElements] = useState([]);
  const {
    properties: { properties },
    displays: { displays, width, height },
    idDisplay: { id: idDisplay },
  } = useSelector((state) => state.data);

  useEffect(() => {
    if (idDisplay !== '') {
      const newElements = Object.values(displays[idDisplay].elements);
      setElements(newElements);
    } else {
      setElements([]);
    }
  }, [displays, idDisplay]);

  useEffect(() => {
    const newSnappable = [
      ...Object.keys(displays[idDisplay].elements).map((item) => `#${item}`),
      '#snapContainer',
    ];
    setSnappable(newSnappable);
  }, [elements]);

  useEffect(() => {
    if (stateDrop) {
      const [x, y] = stateDrop;
      const newElement = {
        ...properties,
        idDisplay,
        id: `i${Date.now()}`,
        pointX: std[properties.pointX] ?? properties.pointX,
        pointY: std[properties.pointY] ?? properties.pointY,
        locationX: x,
        locationY: y,
      };
      dispatch(addElements(newElement));
    }
  }, [stateDrop]);

  const [, drop] = useDrop(() => ({
    accept: 'box',
    collect: (monitor) => ({
      isActive: monitor.canDrop() && monitor.isOver(),
    }),
    drop: (p, monitor) => {
      const clientOffset = monitor.getClientOffset();
      const container = document.getElementById('snapContainer');
      setStateDrop([
        clientOffset.x - container.offsetLeft,
        clientOffset.y - container.offsetTop,
      ]);
      return {
        backgroundColor: 'lightblue',
      };
    },
  }));

  const customAble = (id) => ({
    name: 'tooltool',
    render(moveable) {
      const { renderPoses } = moveable.state;

      return (
        <button
          key={`D${id}`}
          onClick={() => {
            dispatch(deleteElement({ id, idDisplay }));
          }}
          style={{
            position: 'absolute',
            transform: `translate(-50%, -50%) translate(${renderPoses[1][0]}px, ${renderPoses[1][1]}px) translateZ(-50px)`,
            zIndex: 100,
          }}
        >
          X
        </button>
      );
    },
  });
  return (
    <div
      ref={drop}
      className='dropList'
      style={{
        width: idDisplay ? `${width}px` : '1024px',
        height: idDisplay ? `${height}px` : '600px',
      }}
    >
      <div
        id='snapContainer'
        className='snapContainer'
        style={{
          backgroundColor: idDisplay ? displays[idDisplay].backgroundColor : '',
          width: idDisplay ? `${width}px` : '1024px',
          height: idDisplay ? `${height}px` : '600px',
        }}
      >
        {elements.map((element) => (
          <div key={element.id}>
            <div
              id={element.id}
              className='element'
              style={{
                width: `${element.width}px`,
                height: `${element.height}px`,
                fontSize: `${element.sizeFont / pt}px`,
                transformOrigin: `${element.pointX}% ${element.pointY}%`,
                transform: `translate(${element.locationX}px, ${element.locationY}px) rotate(${element.rotation}deg)`,
              }}
            >
              {items[element.type]({
                ...element,
                alt: t('interface.uploadImage'),
              })}
            </div>
            <Moveable
              target={`#${element.id}`}
              snapGap
              draggable
              resizable
              rotatable
              snappable
              isDisplaySnapDigit
              snapThreshold={5}
              elementGuidelines={snappable}
              snapContainer='.snapContainer'
              snapDirections={{
                top: true,
                left: true,
                bottom: true,
                right: true,
                center: true,
                middle: true,
              }}
              elementSnapDirections={{
                top: true,
                left: true,
                bottom: true,
                right: true,
                center: true,
                middle: true,
              }}
              bounds={{
                left: 0,
                top: 0,
                right: 0,
                bottom: 0,
                position: 'css',
              }}
              ables={[customAble(element.id)]}
              tooltool
              onDrag={(e) => {
                e.target.style.transform = e.transform;
              }}
              onDragEnd={(e) => {
                if (e.lastEvent) {
                  dispatch(
                    updateElements({
                      idDisplay,
                      id: element.id,
                      locationX: e.lastEvent.translate[0],
                      locationY: e.lastEvent.translate[1],
                    }),
                  );
                }
              }}
              onResize={(e) => {
                e.target.style.width = `${e.width}px`;
                e.target.style.height = `${e.height}px`;
                e.target.style.transform = e.drag.transform;
              }}
              onResizeEnd={(e) => {
                const rect = e.moveable.getRect();
                dispatch(
                  updateElements({
                    idDisplay,
                    id: element.id,
                    width: rect.offsetWidth,
                    height: rect.offsetHeight,
                  }),
                );
              }}
              onRotate={(e) => {
                e.target.style.transform = e.drag.transform;
              }}
              onRotateEnd={(e) => {
                const rect = e.moveable.getRect();
                dispatch(
                  updateElements({
                    idDisplay,
                    id: element.id,
                    rotation: rect.rotation,
                  }),
                );
              }}
            />
          </div>
        ))}
      </div>
    </div>
  );
};

export default AreaDnd;
