/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  width: '1024',
  height: '600',
  displays: {},
};

const sliceElements = createSlice({
  name: 'sliceElements',
  initialState,
  reducers: {
    updateDisplay: (state, { payload }) => {
      const { width, height } = payload;
      state.width = width;
      state.height = height;
    },
    addDisplay: (state, { payload }) => {
      state.displays[payload.id] = payload;
    },
    deleteDisplay: (state, { payload }) => {
      const { [payload]: id, ...otherElements } = state.displays;
      state.displays = otherElements;
    },
    addElements: (state, { payload }) => {
      const { idDisplay, ...properties } = payload;
      state.displays[idDisplay].elements[payload.id] = properties;
    },
    updateElements: (state, { payload }) => {
      const { idDisplay, ...properties } = payload;
      const newElement = {
        ...state.displays[idDisplay].elements[payload.id],
        ...properties,
      };
      state.displays[idDisplay].elements[payload.id] = newElement;
    },
    deleteElement: (state, { payload }) => {
      const { idDisplay, id } = payload;
      const { [id]: l, ...otherElements } = state.displays[idDisplay].elements;
      state.displays[idDisplay].elements = otherElements;
    },
  },
});

export const {
  updateElements,
  addElements,
  deleteElement,
  addDisplay,
  deleteDisplay,
  updateDisplay,
} = sliceElements.actions;

export default sliceElements.reducer;
