import Modal from 'react-bootstrap/Modal';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { deleteDisplay } from '../../slices/sliceDisplays';
import { updateIdDisplay } from '../../slices/sliceIdDIsplay';

const DeleteDisplay = (props) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const { onHide } = props;
  const {
    displays: { displays },
    idDisplay: { id },
  } = useSelector((state: { data }) => state.data);
  const arrDisplay = Object.keys(displays).length;

  return (
    <>
      <Modal.Header closeButton>
        <Modal.Title>{t('interface.deleteDisplay')}</Modal.Title>
      </Modal.Header>
      <Modal.Body>{t('interface.confirmation')}</Modal.Body>
      <Modal.Footer>
        <button onClick={onHide}>{t('interface.cansel')}</button>
        <button
          onClick={(): void => {
            if (arrDisplay > 0) {
              dispatch(
                updateIdDisplay(
                  Object.keys(displays)
                    .filter((item) => item !== id)
                    .reverse()[0] ?? '',
                ),
              );
            } else {
              dispatch(updateIdDisplay(''));
            }
            dispatch(deleteDisplay(id));
            onHide();
          }}
        >
          {t('interface.delete')}
        </button>
      </Modal.Footer>
    </>
  );
};
export default DeleteDisplay;
