/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  id: '',
};

const sliceIdDisplay = createSlice({
  name: 'sliceIdDisplay',
  initialState,
  reducers: {
    updateIdDisplay: (state, { payload }) => {
      state.id = payload;
    },
  },
});

export const { updateIdDisplay } = sliceIdDisplay.actions;

export default sliceIdDisplay.reducer;
