import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import { updateIdDisplay } from '../../slices/sliceIdDIsplay';
import AreaDnd from '../areaDnd/AreaDnd';
import './Display.css';

const Display = () => {
  const dispatch = useDispatch();
  const {
    displays: { displays },
  } = useSelector((state) => state.data);
  const [displaysArr, setDisplaysArr] = useState([]);
  const [currentDisplay, setCurrentDisplay] = useState('');

  const arrDisplay = Object.keys(displays).length;

  useEffect(() => {
    const newDisplayArr = Object.values(displays).filter((item) => item.id);
    setDisplaysArr(newDisplayArr);
  }, [displays]);
  useEffect(() => {
    if (arrDisplay > 0) {
      setCurrentDisplay(Object.keys(displays).reverse()[0]);
      dispatch(updateIdDisplay(Object.keys(displays).reverse()[0]));
    } else {
      setCurrentDisplay('');
      dispatch(updateIdDisplay(''));
    }
  }, [arrDisplay]);

  return (
    <div className='display'>
      {displaysArr.map(({ name, id }) => (
        <button
          href='#'
          key={id}
          onClick={() => {
            setCurrentDisplay(id);
            dispatch(updateIdDisplay(id));
          }}
        >
          {name}
        </button>
      ))}
      {currentDisplay && <AreaDnd currentDisplay={currentDisplay} />}
    </div>
  );
};

export default Display;
