import { configureStore, combineReducers } from '@reduxjs/toolkit';
import slicePropertiesElement from './slicePropertiesElement';
import sliceIdDIsplay from './sliceIdDIsplay';
import sliceDisplays from './sliceDisplays';
import sliceModal from './sliceModal';

export default configureStore({
  reducer: {
    data: combineReducers({
      displays: sliceDisplays,
      properties: slicePropertiesElement,
      modal: sliceModal,
      idDisplay: sliceIdDIsplay,
    }),
  },
});
