import { useTranslation } from 'react-i18next';
import Modal from 'react-bootstrap/Modal';
import { useDispatch } from 'react-redux';
import { useState } from 'react';
import { addDisplay, updateDisplay } from '../../slices/sliceDisplays';

const CreateDisplay = (props) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const [width, setWidth] = useState(1024);
  const [height, setHeight] = useState(600);
  const [name, setName] = useState('');
  const [backgroundColor, setBackgroundColor] = useState('#000000');
  const { onHide } = props;

  const onChange =
    (type) =>
    ({ target: { value } }) => {
      switch (type) {
        case 'width':
          setWidth(value);
          break;
        case 'height':
          setHeight(value);
          break;
        case 'backgroundColor':
          setBackgroundColor(value);
          break;
        default:
          setName(value);
          break;
      }
    };
  return (
    <>
      <Modal.Header closeButton>
        <Modal.Title>{t('interface.addDisplay')}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {' '}
        <label htmlFor='name'>{t('interface.name')}</label>
        <input
          type='text'
          id='nameD'
          value={name}
          onChange={onChange('name')}
        />
        <label htmlFor='name'>{t('interface.width')}</label>
        <input
          type='number'
          id='widthD'
          value={width}
          onChange={onChange('width')}
        />
        <label htmlFor='name'>{t('interface.height')}</label>
        <input
          type='number'
          id='heightD'
          value={height}
          onChange={onChange('height')}
        />
        <label htmlFor='backgroundColor'>
          {t('interface.backgroundColor')}
        </label>
        <input
          type='color'
          id='backgroundColor'
          value={backgroundColor}
          onChange={onChange('backgroundColor')}
        />
      </Modal.Body>
      <Modal.Footer>
        <button onClick={onHide}>{t('interface.cansel')}</button>
        <button
          onClick={() => {
            dispatch(
              addDisplay({
                id: `d${Date.now()}`,
                elements: {},
                name,
                backgroundColor,
              }),
            );
            dispatch(
              updateDisplay({
                width,
                height,
              }),
            );
            onHide();
          }}
        >
          {t('interface.create')}
        </button>
      </Modal.Footer>
    </>
  );
};
export default CreateDisplay;
