import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import Modal from 'react-bootstrap/Modal';
import { useState } from 'react';
import { closeModal, openModal } from '../../slices/sliceModal';
import DeleteDisplay from '../deleteDisplay/DeleteDisplay.tsx';
import CreateDisplay from '../createDisplay/CreateDisplay';
import './RightMenu.css';

const RightMenu = () => {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const [action, setAction] = useState('');
  const { isModalShow } = useSelector((state) => state.data.modal);

  const getModal = (type) => () => {
    setAction(type);
    dispatch(openModal());
  };
  const typesModal = {
    createDisplay: CreateDisplay,
    deleteDisplay: DeleteDisplay,
  };

  const CurrentModal = typesModal[action];
  const onHide = () => {
    dispatch(closeModal());
  };
  return (
    <div className='rightMenu'>
      <button onClick={getModal('createDisplay')}>
        {t('interface.createDisplay')}
      </button>
      <button onClick={getModal('deleteDisplay')}>
        {t('interface.deleteDisplay')}
      </button>
      <Modal show={isModalShow} onHide={onHide}>
        {isModalShow && <CurrentModal onHide={onHide} />}
      </Modal>
    </div>
  );
};
export default RightMenu;
